import { Component, OnInit, ViewChild } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import {
  LoadingController,
  ModalController,
  NavController,
  ToastController,
} from '@ionic/angular';
import { plainToClass } from 'class-transformer';
import { BasePage } from 'src/app/base/base-page';
import { ArticleCategoryBean } from 'src/app/beans/article-category-bean';
import { PagerBean } from 'src/app/beans/pager-bean';
import { ResponseBean } from 'src/app/beans/response-bean';
import { StatusContentComponent } from 'src/app/components/status-content/status-content.component';
import { ScreenFitService } from 'src/app/providers/screen-fit.service';
import { ThemeService } from 'src/app/providers/theme.service';
import { ApiService } from 'src/app/services/api.service';
import { RuntimeService } from 'src/app/services/runtime.service';

@Component({
  selector: 'app-submit-article',
  templateUrl: './submit-article.page.html',
  styleUrls: ['./submit-article.page.scss'],
})
export class SubmitArticlePage extends BasePage implements OnInit {
  @ViewChild('appStatusContent', { static: false })
  appStatusContent: StatusContentComponent;
  PICTURE_LIMIT_MAX: number = 12;
  KEYWORD_LIMIT_MAX: number = 6;

  mediaType: 'picture' | 'video' = 'picture';
  mediaTypeName: string = '图片';
  pictureList: File[] = [];
  pictureURLList: string[] = [];
  coverImage: File = null;
  coverImageURL: string = '';
  categoryList: ArticleCategoryBean[] = [];
  currentCategory: ArticleCategoryBean = null;
  keyword: string = '';
  keywordList: string[] = [];
  formData = {
    title: '',
    coverImage: '',
    videoURL: '',
    summary: '',
    author: '',
    sourceURL: '',
    articleCategory: '',
    articleContent: '',
    email: '',
    wechat: '',
    phoneNumber: '',
    linkURL: '',
  };

  constructor(
    protected loadingCtrl: LoadingController,
    protected toastCtrl: ToastController,
    protected runtime: RuntimeService,
    protected screenFitService: ScreenFitService,
    private navCtrl: NavController,
    private modalCtrl: ModalController,
    private api: ApiService,
    private router: Router,
    private sanitizer: DomSanitizer,
    private routeInfo: ActivatedRoute,
    private themeService: ThemeService
  ) {
    super(loadingCtrl, toastCtrl, runtime, screenFitService);
    this.routeInfo.queryParams.subscribe((params) => {
      if (params.media) {
        this.mediaType = params.media;
        this.mediaTypeName = this.mediaType == 'video' ? '视频' : '图片';
      }
    });
  }

  ngOnInit() {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        if (event.url == '/submit-article') {
          this.setPageTitle('SubmitArticle');
        }
      }
    });
  }

  async initData() {
    // fetch data or load cache
    await Promise.all([this.fetchCategories()]);

    this.appStatusContent.invokeSuccess();
  }

  ionViewWillEnter() {
    this.setPageTitle('SubmitArticle');
  }

  ionViewDidEnter() {
    (async () => {
      await this.initData();
    })().catch((error) => {
      console.error('[init data errror]', error);
      this.appStatusContent.invokeError();
    });
  }

  onRetry() {
    (async () => {
      await this.initData();
    })().catch((error) => {
      console.error('[init data errror]', error);
      this.appStatusContent.invokeError();
    });
  }

  back() {
    this.navCtrl.back();
  }

  onCoverImageFileChange(event: any) {
    const files = <FileList>event.target.files;
    if (files.length === 0) {
      return;
    }
    this.coverImage = files[0];
    this.coverImageURL = URL.createObjectURL(this.coverImage);
    const inputElement = <HTMLInputElement>document.getElementById('coverImageFile');
    inputElement.value = '';
  }

  onClickUploadCoverImageFile() {
    document.getElementById('coverImageFile').click();
  }

  onClickUploadMediaFile() {
    document.getElementById('mediaFile').click();
  }

  onClickRemoveCoverImage() {
    this.coverImage = null;
    this.coverImageURL = '';
  }

  onMediaFileChange(event: any) {
    const files = <FileList>event.target.files;
    if (files.length === 0) {
      return;
    }
    for (let index = 0; index < files.length; index++) {
      if (
        this.pictureList &&
        this.pictureList.length < this.PICTURE_LIMIT_MAX
      ) {
        const mFile = files[index];
        const url = URL.createObjectURL(mFile);
        this.pictureList.push(mFile);
        this.pictureURLList.push(url);
      }
    }
    const inputElement = <HTMLInputElement>document.getElementById('mediaFile');
    inputElement.value = '';
  }

  onClickRemovePicture(index: number) {
    this.pictureList.splice(index, 1);
    this.pictureURLList.splice(index, 1);
  }

  onClickCategoryItem(item: ArticleCategoryBean) {
    this.currentCategory = item;
  }

  onKeyup(event: KeyboardEvent) {
    const code = event.code;
    switch (code) {
      case 'Enter':
        if (this.keyword) {
          if (this.keywordList.length < this.KEYWORD_LIMIT_MAX) {
            this.keywordList.push(this.keyword);
            this.keyword = '';
          } else {
            this.showToast(
              `最多允许添加${this.KEYWORD_LIMIT_MAX}个关键字`
            );
          }
        }
        break;
    }
  }

  onClickRemoveKeyword(index: number) {
    this.keywordList.splice(index, 1);
  }

  async fetchCategories() {
    const response: ResponseBean<PagerBean<ArticleCategoryBean>> = plainToClass(
      ResponseBean,
      await this.api.fetchCategoryList(null)
    );
    if (response.code == 200) {
      if (response.result.data && response.result.data.length > 0) {
        this.categoryList = response.result.data;
        this.currentCategory = this.categoryList[0];
      }
    } else {
      return Promise.reject({ code: response.code, message: response.message });
    }
  }

  onImageFileChange(event: any) {
    console.error(event);
  }
}
